
"use client";

import { Sidebar } from "flowbite-react";
import {HiChartSquareBar, HiCash, HiClipboard, HiUser, HiOutlinePlus } from "react-icons/hi";
import { Modal } from "flowbite-react";

import { useState } from "react";

import ModalMov from "./ModalMov";

export default function DashboardSidebar() {

  const [openModalMov, setOpenModalMov] = useState(false);

  return (
    <Sidebar aria-label="Sidebar with content separator example" className="h-screen">
        <Modal show={openModalMov} onClose={() => setOpenModalMov(false)}>
          <ModalMov/> 
        </Modal>
        <Sidebar.Logo href="#" img="logo-black.png" imgAlt="Flowbite logo" >
        A.G.I.O.T.A
      </Sidebar.Logo>
      <Sidebar.Items>
        <Sidebar.ItemGroup>
          <Sidebar.Item href="dashboard" icon={HiChartSquareBar}>
            Painel
          </Sidebar.Item>
          <Sidebar.Item href="dashboard/extrato" icon={HiCash}>
            Extrato
          </Sidebar.Item>
          <Sidebar.Item href="dashboard/contatos" icon={HiClipboard}>
            Contatos
          </Sidebar.Item>
          <Sidebar.Item href="#" icon={HiOutlinePlus} onClick={() => setOpenModalMov(true)}>
            Adicionar movimentação
          </Sidebar.Item>
        </Sidebar.ItemGroup>
        <Sidebar.ItemGroup>
          <Sidebar.Item href="#" icon={HiUser }>
             Perfil
          </Sidebar.Item>
        </Sidebar.ItemGroup>
      </Sidebar.Items>
    </Sidebar>
    
  );
}
